@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");

@keyframes cometFly {
    0% {
        opacity: 0;
        transform: translate(300px, -300px);
      }
      40% {
        opacity: 1;
      }
      100% {
        opacity: 1;
        transform: translate(0, 0);
      }
}

.about-content {
    height: 48vh;
    background-color: #2C2458;
    background-image: url("../public/images/aboutpage.png");
    transition: height 0.6s ease;
}

.about-content button {
    background-color: #483C82;
    border: none;
    font-family: 'IBM Plex Mono';
    font-weight: 700;
    color: white;
    border-radius: 25px;
    padding: 15px;
    font-size: 1.3rem;
    box-shadow: 1px 3px 2px #1a162f;
    margin: 25px 40px;
}

.about-content button:hover {
    transform: translateY(2px);
    box-shadow: 1px 4px 4px #141122;
}

.about-content button:active {
    transform: scale(0.98);
    box-shadow: 1px 4px 3px #19162a;
}

.no-button {
    display: flex;
    transform: translate(0, -10%);
    transition: transform 0.6s ease;
}

.text {
    display: flex;
    color: white;
    justify-content: center;
    align-items: center;
    font-family: 'IBM Plex Mono';
    font-size: 5rem;
    margin: 0;
    padding: 0;
    line-height: 0.1em;
    transform: translate(0, -10%);
    transition: transform 0.6s ease, gap 0.6s ease;
}

.text h1 {
    transition: transform 0.7s ease;
}

.text img {
    vertical-align: middle;
    margin: 0px 15px;
}

.helmet {
    width: 300px;
    transition: transform 0.7s ease;
}

.comet {
  opacity: 0;
  transform: translate(0, 0);
  transition: opacity 0.3s ease;
  animation-fill-mode: forwards;
}

.fly-in {
    animation: cometFly 1.5s ease-out forwards;
}

.hidden {
    opacity: 0;
}

.hide-text {
    display: none;
}

/* expanded screen */
.expanded {
    height: 70vh;
    display: flex;
    justify-content: center;
}

.expanded button {
    display: none;
}

.expanded .no-button {
    transform: translate(0, 0%);
}

.expanded .text {
    transform: translate(0, 0%);
    gap: 22%;
}

.expanded .text h1 {
    transform: scale(1.5);
}

.expanded .helmet {
    transform: scale(1.7);
}

.expanded .comet {
    display: none;
}

.show-text {
    position: absolute;
    line-height: 1.3;
    font-size: 1.1rem;
    text-align: center;
    top: 16%;
    max-width: 290px;
    font-family: 'Roboto', serif;
    font-weight: 700;
}